.box .overlay,
.overlay-wrapper .overlay {
  z-index: 50;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 3px; }

.box .overlay > .fa,
.overlay-wrapper .overlay > .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -15px;
  color: #000;
  font-size: 30px; }

.box .overlay.dark,
.overlay-wrapper .overlay.dark {
  background: rgba(0, 0, 0, 0.5); }

.box-header:before,
.box-body:before,
.box-footer:before,
.box-header:after,
.box-body:after,
.box-footer:after {
  content: " ";
  display: table; }

.box-header:after,
.box-body:after,
.box-footer:after {
  clear: both; }

.box-header {
  color: #444;
  display: block;
  padding: 10px;
  position: relative; }

.box-header.with-border {
  border-bottom: 1px solid #f4f4f4; }

.collapsed-box .box-header.with-border {
  border-bottom: none; }

.box-header > .fa,
.box-header > .glyphicon,
.box-header > .ion,
.box-header .box-title {
  display: inline-block;
  font-size: 18px;
  margin: 0;
  line-height: 1; }

.box-header > .fa,
.box-header > .glyphicon,
.box-header > .ion {
  margin-right: 5px; }

.box-header > .box-tools {
  position: absolute;
  right: 10px;
  top: 5px; }

.box-header > .box-tools [data-toggle="tooltip"] {
  position: relative; }

.box-header > .box-tools.pull-right .dropdown-menu {
  right: 0;
  left: auto; }

.btn-box-tool {
  padding: 5px;
  font-size: 12px;
  background: transparent;
  color: #97a0b3; }

.open .btn-box-tool,
.btn-box-tool:hover {
  color: #606c84; }

.btn-box-tool.btn:active {
  box-shadow: none; }

.box-body {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  padding: 10px; }

.no-header .box-body {
  border-top-right-radius: 3px;
  border-top-left-radius: 3px; }

.box-body > .table {
  margin-bottom: 0; }

.box-body .fc {
  margin-top: 5px; }

.box-body .full-width-chart {
  margin: -19px; }

.box-body.no-padding .full-width-chart {
  margin: -9px; }

.box-body .box-pane {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 3px; }

.box-body .box-pane-right {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 0; }

.box-footer {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  border-top: 1px solid #f4f4f4;
  padding: 10px;
  background-color: #ffffff; }

.chart-legend {
  margin: 10px 0; }

@media (max-width: 991px) {
  .chart-legend > li {
    float: left;
    margin-right: 10px; } }

.box-comments {
  background: #f7f7f7; }

.box-comments .box-comment {
  padding: 8px 0;
  border-bottom: 1px solid #eee; }

.box-comments .box-comment:before,
.box-comments .box-comment:after {
  content: " ";
  display: table; }

.box-comments .box-comment:after {
  clear: both; }

.box-comments .box-comment:last-of-type {
  border-bottom: 0; }

.box-comments .box-comment:first-of-type {
  padding-top: 0; }

.box-comments .box-comment img {
  float: left; }

.box-comments .comment-text {
  margin-left: 40px;
  color: #555; }

.box-comments .username {
  color: #444;
  display: block;
  font-weight: 600; }

.box-comments .text-muted {
  font-weight: 400;
  font-size: 12px; }
